import React from 'react';
import styled from 'styled-components';

const MainDiv = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d2d2d2;
  height: 50vh;
  width: 100vw;
  padding: 0 1.5rem;
`;
const Footer = () => {
    return <MainDiv></MainDiv>;
}

export default Footer;